<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input
            v-model="filters.name"
            placeholder="用户名称"
            @keydown.enter.native="getOrders"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getOrders">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addDialog = true">新增</el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!--列表-->
    <el-table :data="orders" highlight-current-row style="width: 100%">
      <el-table-column type="selection" width="55"> </el-table-column>

      <el-table-column prop="id" label="id" width="120" sortable>
      </el-table-column>
      <el-table-column prop="username" label="登录名" width="200" sortable>
      </el-table-column>
      <el-table-column prop="real_name" label="用户名" width="200" sortable>
      </el-table-column>

      <el-table-column prop="group.group_type_cn" label="用户组" sortable>
      </el-table-column>

      <el-table-column prop="auth.auth_type" label="权限组" sortable>
      </el-table-column>

      <el-table-column label="是否冻结" sortable>
        <template scope="scope">
          <span v-if="scope.row.is_freeze">是</span>
          <span v-else>否</span>
        </template>
      </el-table-column>

      <el-table-column label="操作" width="320">
        <template scope="scope">
          <!-- <el-button size="small" @click="handleEdit(scope.$index, scope.row)">查看详情</el-button>-->
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button
          >

          <el-button
            type="danger"
            size="small"
            @click="delOrder(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="pageSize"
        :total="total"
        style="float: right"
      >
      </el-pagination>
    </el-col>

    <!--新增dialog  -->
    <el-dialog title="新增用户" :visible.sync="addDialog">
      <el-form class="demo-form-inline" :model="addUserinfo" label-width="80px">
        <el-form-item label="登录名">
          <el-input
            v-model="addUserinfo.username"
            auto-complete="off"
          ></el-input>
        </el-form-item>

         <el-form-item label="密码">
          <el-input
            v-model="addUserinfo.password"
            type="password"
          ></el-input>
        </el-form-item>

        <el-form-item label="用户名">
          <el-input
            v-model="addUserinfo.real_name"
            auto-complete="off"
          ></el-input>
        </el-form-item>

        <el-form-item label="用户组">
          <el-select v-model="addUserinfo.group" placeholder="请选择">
            <el-option
              v-for="item in group"
              :key="item.id"
              :label="item.group_type_cn"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>


        <el-form-item label="权限组">
          <el-select v-model="addUserinfo.auth" placeholder="请选择">
            <el-option
              v-for="item in auth"
              :key="item.id"
              :label="item.auth_type"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="是否冻结">
          <el-switch
            v-model="addUserinfo.is_freeze"
            active-value="1"
            inactive-value="0"
          >
          </el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false">取 消</el-button>
        <el-button type="primary" @click="addUser()">提交</el-button>
      </div>
    </el-dialog>

    <!-- 新增dialog 结束 -->

    <!--编辑dialog  -->
    <el-dialog title="编辑供应商" :visible.sync="editDialog">
      <el-form
        class="demo-form-inline"
        :model="editUserinfo"
        label-width="80px"
      >
        <el-form-item label="登录名">
          <el-input
            v-model="editUserinfo.username"
            auto-complete="off"
          ></el-input>
        </el-form-item>

 <el-form-item label="密码">
          <el-input
            v-model="editUserinfo.password"
            type="password"
          ></el-input>
        </el-form-item>

        <el-form-item label="用户名">
          <el-input
            v-model="editUserinfo.real_name"
            auto-complete="off"
          ></el-input>
        </el-form-item>

        <el-form-item label="用户组">
          <el-select v-model="editUserinfo.group" placeholder="请选择">
            <el-option
              v-for="item in group"
              :key="item.id"
              :label="item.group_type_cn"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>

 <el-form-item label="权限组">
          <el-select v-model="editUserinfo.auth" placeholder="请选择">
            <el-option
              v-for="item in auth"
              :key="item.id"
              :label="item.auth_type"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="是否冻结">
          <el-switch
            v-model="editUserinfo.is_freeze"
            active-value="1"
            inactive-value="0"
          >
          </el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editDialog = false">取 消</el-button>
        <el-button type="primary" @click="editUser()">提交</el-button>
      </div>
    </el-dialog>

    <!-- 编辑dialog 结束 -->
  </section>
</template>


<script>
import { accMul } from "../../util/calculate.js";
export default {
  data() {
    return {
      filters: {},
      orders: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      addUserinfo: {
        group: 2,
      },
      addDialog: false,
      editDialog: false,
      editUserinfo: {},

      // 用户组
      group: [],
      // 权限组
      auth:[],
    };
  },
  created() {
    this.getGroup();

    this.getOrders();

    this.getAuth();
  },
  methods: {
    async getAuth(){
      const { data } = await this.$axios('auth');
      this.auth = data
      console.log(this.auth)
    },

    async getGroup() {
      const { data } = await this.$axios("UserGroup/");
      this.group = data;
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getOrders();
    },
    async getOrders() {
      const data = await this.$axios("user/", {
        params: {
          search: this.filters.name,
          page: this.page,
        },
      });

      console.log(data);

      this.orders = data.data;
      this.total = data.count;
    },
    handleEdit(index, row) {
      this.editDialog = true;
      this.editUserinfo = Object.assign({ password : '' }, row);
      if(row.group && row.group.id){
        this.editUserinfo.group = row.group.id
      }

      if(row.auth && row.auth.id){
        this.editUserinfo.auth = row.auth.id
      }

    },
    async delOrder(index, row) {
      let res = await this.$axios.delete(`user/${row.id}`);
      console.log(res);
      this.getOrders();
    },
    async addUser() {
      let res = await this.$axios.post("user/", this.addUserinfo);
      //console.log(res)
      this.addDialog = false;
      this.getOrders();
    },
    async editUser() {


      let subdata = Object.assign({}, this.editUserinfo)
      // 判断是否有密码， 有提交密码， 没有就不修改密码
      // if (this.editDialog.password && this.editDialog.password.length != ''){
        
      // }else{
      //   console.log(this.editDialog.password)
      //   delete subdata.password
      // }
       if(subdata.password === ''){
         delete subdata.password
       }

            console.log(subdata)
      let res = await this.$axios.put(
        `user/${this.editUserinfo.id}/`,
        subdata
      );
      console.log(res);
      this.editDialog = false;
      this.getOrders();
    },
  },
};
</script>

<style>
</style>


